.background {
  width: 100%;
  height: fit-content;
}

.background_image1 {
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 50%;
  background: #f5f6fb;
  border-radius: 0 0 0 38%;
  z-index: -99;
}

.background_multicabbage {
  background-image: url("../../img/background/cabbage-background-top.png");
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 0 0 0 38%;
  z-index: 0;
}

.background_image2 {
  background-image: url("../../img/background/two-cabbages.png");
  background-repeat: no-repeat;
  position: absolute;
  z-index: -50;
  top: 60%;
  left: 10%;
  width: 50%;
  height: 50%;
  margin-bottom: unset;
  padding: unset;
  animation: float 6s ease-in-out infinite;
}
@keyframes float {
  0% {
    transform: translatey(0px);
  }
  50% {
    transform: translatey(-20px);
  }
  100% {
    transform: translatey(0px);
  }
}

@media (min-width: 772px) {
  .background_image1 {
    border-radius: 0 0 0 140px;
    width: 100%;
  }
  .background_image2 {
    background-image: url("../../img/background/two-cabbages.png");
    background-repeat: no-repeat;
    position: absolute;
    z-index: 0;
    top: 65%;
    left: 10%;
    width: 25%;
    height: 50%;
  }
}

@media (min-width: 1280px) {
  .background_image1 {
    width: 100%;
    height: 75%;
    border-radius: 0 0 0 140px;
  }
  .background_image2 {
    background-image: url("../../img/background/two-cabbages.png");
    background-repeat: no-repeat;
    background-position: 20%;
    position: absolute;
    /* display: flex; */
    z-index: 0;
    /* margin-top: 10%; */
    margin-bottom: unset;
    padding: unset;
